export const font = (text) => {
  if (text.length === 0) return [];
  const charMap = [
    {
      text: applyCharMap(futureAlienCharMap, text),
    },
    {
      text: applyCharMap(squiggle6CharMap, text),
    },
    {
      text: applyCharMap(asianStyle2CharMap, text),
    },
    {
      text: applyCharMap(asianStyleCharMap, text),
    },
    {
      text: applyCharMap(squaresCharMap, text),
    },
    {
      text: applyCharMap(squiggle4CharMap, text),
    },
    {
      text: applyCharMap(neonCharMap, text),
    },
    {
      text: applyCharMap(squiggle3CharMap, text),
    },
    {
      text: applyCharMap(monospaceCharMap, text),
    },
    {
      text: applyCharMap(boldItalicCharMap, text),
    },
    {
      text: applyCharMap(boldCharMap, text),
    },
    {
      text: applyCharMap(boldSansCharMap, text),
    },
    {
      text: applyCharMap(italicCharMap, text),
    },
    {
      text: applyCharMap(squiggle2CharMap, text),
    },
    {
      text: applyCharMap(currencyCharMap, text),
    },
    {
      text: applyCharMap(symbolsCharMap, text),
    },
    {
      text: applyCharMap(squiggle2CharMap, text),
    },
    {
      text: applyCharMap(greekCharMap, text),
    },
    {
      text: applyCharMap(bentTextCharMap, text),
    },
    {
      text: applyCharMap(upperAnglesCharMap, text),
    },
    {
      text: applyCharMap(subscriptCharMap, text),
    },
    {
      text: applyCharMap(superscriptCharMap, text),
    },
    {
      text: applyCharMap(squiggleCharMap, text),
    },
    {
      text: applyCharMap(doubleStruckCharMap, text),
    },
    {
      text: applyCharMap(medievalCharMap, text),
    },
    {
      text: applyCharMap(cursiveCharMap, text),
    },
    {
      text: applyCharMap(invertedSquaresCharMap, text),
    },
    {
      text: applyCharMap(wideTextCharMap, text),
    },
    {
      text: applyCharMap(oldEnglishCharMap, text),
    },
    {
      text: applyCharMap(squiggle5CharMap, text),
    },
    {
      text: cuteText(text),
    },
    {
      text: cuteText(text),
    },
    {
      text: cuteText(text),
    },
    {
      text: cuteText(text),
    },
  ];
  return charMap;
};
//character Function
function applyCharMap(map, text) {
  let out = "";
  for (let c of text.split("")) {
    if (map[c] !== undefined) out += map[c];
    else if (map[c.toLowerCase()] !== undefined) out += map[c.toLowerCase()];
    else out += c;
  }
  return out;
}
//cutetext
function cuteText(text) {
    var bounds = boundingString(Math.floor(Math.random() * 2) + 1);
    return bounds + "    " + text.split(/([!?.]+)/gi).map(cuteSentence).join("") + "    " + bounds
}
function cuteSentence(sentence) {
    return sentence.split(/([\s,]+)/gi).map(cuteWord).join("");
}


//character Function
function boundingString(n) {
    return randomElement([foodString, twinkleString, animalString, flourishString])(n);
}
function foodString(n) {
    return new Array(n + 1).join('0').split('').map(function(a) {
        return randomElement(food);
    }).join('  ');
}

function twinkleString(n) {
    return new Array(n + 1).join('0').split('').map(function() {
        return randomElement(twinkles);
    }).join("");
}

function animalString(n) {
    return new Array(n + 1).join('0').split('').map(function(a) {
        return randomElement(animals);
    }).join('  ');
}

function flourishString(n) {
    return randomElement(flourish);
}
function cuteWord(word) {
    //remember to handle exclamations, empty strings, commas etc. etc.!!
    if (word === "" || /[[!?.\s,]+]/gi.test(word)) return word;
    //TODO: random turn o's into flowers, turn quotes into unicode and ! and ?
    word = roundReplace(word);
    word = punctReplace(word);
    word = emojiReplace(word);
    return scriptify(word);
}
function roundReplace(text) {
    return text.split("").map(function(a) {
        if (a.toLowerCase() === "o" || a === "0") {
            return randomElement(round);
        } else {
            return a;
        }
    }).join("");
}
function randomElement(a) {
    return a[Math.floor(Math.random() * a.length)];
}
function punctReplace(word) {
    return word.split("")
        .map(function(a) {
            if (a === "!") return randomElement(["", ""]);
            else if (a === "?") return randomElement(["", "?"]);
            else if (a === "\"") return quotes();
            else return a;
        }).join("");
}
function emojiReplace(word) {
    return word
        .replace(":)", randomElement(emoji[":)"]))
        .replace("(:", randomElement(emoji[":)"]))
        .replace("^^", randomElement(emoji["^^"]))
        .replace(":P", randomElement(emoji[":p"]))
        .replace(":p", randomElement(emoji[":p"]))
        .replace(":D", randomElement(emoji[":D"]))
        .replace("<3", randomElement(emoji["<3"]))
        .replace("(y)", randomElement(emoji["(y)"]))
        .replace("(Y)", randomElement(emoji["(y)"]));
}
var qi = -1;
var qa = ["", ""];

function quotes() {
    qi++;
    if (qi === 2) qi = 0;
    return qa[qi];
}
